import React, { Component } from 'react'
import './aboutme.css';

export default class AboutMe extends Component {
  render() {
    return (
      <div id="about-me-container">
        <header>
          <div id="about-me-header">
          </div>
        </header>
        <main>
          <div id="about-me-body">
            <div className="img-wrapper">
              <div className="paulo-image">
                <img src="./paulo.jpeg" className="about-me-image" alt="Paulo Gozum Picture" />
              </div>
            </div>
          </div>

          <div className="about-me-text-container">

            <div className="about-me-text-wrapper">
              <h1 className="about-me-heading-text">Hi, I'm Paulo Gozum. Part-time Computer Geek, Part-time Judoka</h1>
               
            </div>

             <div className="about-me-text-wrapper">
               <h2 className="about-me-text">I'm an aspiring Front-end Developer that recently moved to Los Angeles. <br/>
               I am currently </h2>
             </div>
            <div className="about-me-text-wrapper">
              <h1 className="about-me-heading-text">Why Work with Me?</h1>
            </div>
             <div className="about-me-text-wrapper">
              <h2 className="about-me-text">As a Management Information Systems graduate with a passion for building user interfaces, <br/> 
                I seek to bridge the gap between Management and Information Technology. I believe that IT is <br/>
                meant to align with business objectives in order to support a firm's goals and strategies <br/>
                As a Front-end developer, I strive to create intuitive and functional web applications <br/>
                that promotes a company's business objectives in order to maximize growth. Technology is <br/>
                meant to enable organizations through the use of Information Technology. </h2>
            </div>
          </div>
        </main>
      </div>

    )
  }
}
